import React from "react";
import { NavLink } from "react-router-dom";
import styled from "styled-components";

const Form = () => {
  return (
      <Section>
        <Panel>
          <Link to="/form/registration">
            <Card>
              <ImageBox>
                <Image
                  src={require("../../components/images/website.png")}
                  alt="Registration"
                />
              </ImageBox>
              <Text>Registration Form</Text>
            </Card>
          </Link>
          <Link to="/form/exam">
            <Card>
              <ImageBox>
                <Image
                  src={require("../../components/images/checklist (1).png")}
                  alt="Improve/Re-Exam"
                />
              </ImageBox>
              <Text>Re-Exam Improve</Text>
            </Card>
          </Link>
        </Panel>
      </Section>
  );
};

export default Form;
const Section = styled.section`
  min-height: 80vh;
  padding-top: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Panel = styled.div`
  width: 400px;
  padding: 0.5rem 1rem;
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
`;

const Card = styled.div`
  width: 120px;
  min-height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem;
  border: 1px solid rgba(3, 3, 3, 0.3);
  border-radius: 10px;
  margin: 0.5rem;
  cursor: pointer;
`;

const ImageBox = styled.div`
  width: 65%;
  height: auto;
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0.5rem;
`;

const Image = styled.img`
  width: 100%;
  height: auto;
`;

const Text = styled.p`
  font-weight: 600;
  font-size: 0.8rem;
  text-align: center;
  margin-bottom: 0;
  margin-top: 0.5rem;
`;

const Link = styled(NavLink)`
  text-decoration: none;
`;
